.PanelRoot
{
    /* background-color: #ff00f0; */  
    width:100%;
    height:100%;  
    color: #2c2c2b;
    user-select: none; 
    max-width: 800px;
    
    
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: center;
    align-items: center;
}

.PanelContent
{
    position: relative;
    width:96%;
    height:100%; 
}

.RoundCornerBG
{
    position: relative;
    width:100%;
    height: 100%;
    overflow: hidden;
    border-radius: 12px; 
}

 
.PanelBG
{
    background-color: #e6e6e6;
    position: absolute;
    width:100%;
    height:100%;
}
 


.PopupPanelCloseBtn
{
    position: absolute;
    width:50px;
    height: 50px;
    top:10px;
    right:5px;
    object-fit: scale-down;
    cursor: pointer;
}
