.PopupMsgRoot
{
    position: absolute;
    left:10%;
    width:80%;
    top:50%;
    margin-top: -23px;
    height:46px;  
    pointer-events: none;  
    display: flex;
    align-items: center;
    justify-content: center;
}


.PopupMsgMain
{
    position:relative;
    height:46px;
    border-radius: 23px;
    background-color: #3a3a3a;
    transition: opacity 2.0s;
}

.PopupMsgBG
{ 
    position: absolute;
    left:-20px;
    right:-20px;
    height:100%;
    border-radius: 23px;
    background-color: #3a3a3a;
}

.PopupMsgTxt
{ 
    position: relative;
    min-width: 60px;
    height:46px;
    line-height: 46px;
    text-align: center;
    color: aliceblue;
}