.Btn_Hyperlink
{   
    position: relative;
    width:100%; 
    height:46px;   
    display: flex;
    justify-content: flex-start;
    cursor:pointer;  
}

 
.Btn_Hyperlink_Txt
{ 
    position: absolute;  
    color:#2e2e2e;
    text-align: left;
    line-height: 46px; 
    font-size: 15px;
    transition: color 1.0s;
}
