.Loading_BG
{  
 
    position: absolute;
    width:100%;
    height:100%; 
    display: flex;
    justify-content: center;
    align-items: center; 

    background-color: black;
    opacity: 0.8;
}
 
.Loading_Icon
{
    width:30px;
    height:30px;
    /* background-color: aqua; */
}


@media (prefers-reduced-motion: no-preference) {
    .Loading_Icon {
      animation: Loading_Icon_spin infinite 3s linear;
    }
  }
   
  @keyframes Loading_Icon_spin {
    from {
      transform: rotate(0deg);
    }
    to {
      transform: rotate(360deg);
    }
  }
