.Btn_Default
{  
    position: relative;
    width:80%;
    max-width:300px;
    min-width: 80px;
    height:46px;   
    display: flex;
    justify-content: center;
    cursor:pointer;  
}

.Btn_Default_Bg
{
    background-color: #169fcc;
    position: absolute;
    width:100%;
    height:100%;
    border-radius: 23px;
    transition: background-color 1.0s;  
}
 

.Btn_Default_Txt
{ 
    position: absolute;  
    color:#FCFCFC;
    line-height: 46px; 
    font-size: 20px;
}
