.InputLine
{
    position: relative;
    width:100%;
    max-width:720px;
    height:80px; 
    margin-bottom: 10px;  
    /* background-color: black; */
}



.InputLineMain
{
    position: absolute;
    bottom: 0px;
    height: 40px;
    
    left:0px; 
    right:0px;
}

.InputLineTitle
{
    position: absolute; 
    height: 100%;
    line-height: 40px;
    width: 80px;
    left:0px;
    /* background-color: blue; */
    text-align: right;
}


/* ================================== */
.InputTextArea
{
    position: absolute;
    height: 100%;
    left:90px;
    right:20px; 
    overflow: visible;
}

.InputTextAreaBG
{
    position: absolute;
    height: 100%;
    width:100%;
    background-color: white;

    border-style: solid;
    border-radius: 20px;
    border-color: #05aee5;
    border-width: 1px;
}

.InputLineText
{ 
    position: absolute;
    left:20px;
    right:20px;
    height: 100%;
    line-height: 40px;  

    border:none;
    background: transparent;
    outline: none;

    overflow: hidden;
    font-size: 16px;
}

/* ================================== */

.InputLineErrorText
{
    position: absolute;
    height: 20px;
    width:100%;
    bottom: 40px;
    font-size: 12px;
    color:#ff5980;
  
}

/* ================================== */
.InputLineChildrenArea
{
    position: absolute;
    width:100%;
    height:100%;
}
